<template>
    <div>
        <p></p>
        <label class="upload">
            <span></span>
            <input name="addfile" type="file" multiple id="uploadImg" @change="handleUpload($event)" />
        </label>
        <ul class="piclist">
            <li class="clearfix" v-for="(item,index) in steps" :key="index">
                <label>
                    <img :src="`/public/uploads/cookbooks/${item.p}`" alt="">
                </label>
                <textarea name="miaoshu" placeholder="200" :value="item.d" @change="handleTextarea(index,$event)"></textarea>
                <!-- <aside class="side_skill">
                    <span>X</span>
                    <span></span>
                    <span></span>
                    <span>+</span>
                </aside> -->
            </li>
        </ul>
    </div>
</template>
<script>
import {postCookbookPicApi} from '@/api/user';
export default {
    props:["value"],
    data(){
        return{
            steps : this.value
        }
    },
    watch:{
        steps : function (val) {
            this.$emit('input',val);
            this.$emit('on-change',val);
        }
    },
    methods:{
        //
        async handleUpload(e){
            // console.log(e.target.files);
            var files = [...e.target.files];
            files.forEach(async file=>{
                 let fd = new FormData();
                 fd.append('cover',file);
                 let resCookbookPic = await postCookbookPicApi(fd); 
                 if(resCookbookPic && !resCookbookPic.data.code){
                    // console.log(resCookbookPic.data.coverUrl);
                    this.steps.push({p:resCookbookPic.data.coverUrl,d:''});
                    // console.log(this.steps);
                    // console.log(this.data.covers);
                }
            })
        },
        handleTextarea(index,event){
            var val = event.target.value.trim();
            this.steps[index].d = val;

        }
    }
}
</script>

