<template>
    <div class="box_outside">
        <Header :userId="this.$route.params.id" @nickname="handleGetnickname"></Header>
        <section class="self_content">
            <div class="wrap">
                <h2 class="clearfix">
                    <span>{{nickname}}</span>
                    <!-- <a href="/member/recipe_add" class="fr"></a> -->
                </h2>
                <div v-show="!cookbooks.length"></div>
                <ul class="dynamic_list">
                    <li v-for="cookbook in cookbooks" :key="cookbook.id">
                        <div class="self_dynamic">
                            <div class="dynamic_msg">
                                <router-link :to="`/view/${cookbook.id}`" tag="a">
                                    {{cookbook.name}}
                                </router-link>
                            </div>
                            <!-- <div class="dynamic_data">
                                <a href=""></a>
                                <span>|</span>
                                <a href=""></a>
                            </div> -->
                        </div>
                    </li>
                </ul>
                <!-- <button class="more"></button> -->
            </div>
        </section>
        <div class="wrap">
            <Footer></Footer>
        </div>
    </div>
</template>
<script>
import Header from '@/components/member/header.vue';
import Footer from '@/components/footer.vue';

import '@/assets/css/header_user.css';
import '@/assets/css/user_index.css';

import {getSpaceCookbookApi} from '@/api/index';

export default {
    data(){
        return{
            nickname:'',
            params:{
                userId : this.$route.params.id
            },
            cookbooks:[]
        }
    },
    components:{
        Header,
        Footer
    },
    mounted(){
        this.getSpaceCookbook();
    },
    methods:{
        //header
        handleGetnickname(nickname){
            this.nickname = nickname
        },
       async getSpaceCookbook(){
           let res = await getSpaceCookbookApi(this.params);
            if(res && !res.data.code){
                this.cookbooks = res.data.cookbooks;
            }
        }
    }
}
</script>

