<template>
    <div class="center">
        <div class="top-bar">
            <ul class="bar-left left">
                <li><router-link to="/" tag="a"></router-link></li>
                <li><router-link to="/list" tag="a"></router-link></li>
            </ul>
            <div style="float:right"> 
                <img :src="`/public/uploads/avatar/${this.$store.state.user.avatar}`" class="avatar" style="width:32px;height:32px;border-radius:50%">
                <router-link to="/center/my_settings_profile" tag="a">{{this.$store.state.user.username}}</router-link>
                <span>|</span>
                <router-link to="" @click.prevent="logout" tag="a"></router-link> 
            </div>
        </div>
        <div class="c-wrap">
            <div class="c-left">
                <div class="c-menu">
                    <ul>
                        <li>
                            <router-link to="/center/my_settings_profile" class="on" tag="a"></router-link>
                        </li>
                        <li>
                            <router-link to="/center/my_recipe_me" tag="a"></router-link>
                        </li>
                    </ul>
                </div>
            </div>
            <div class="c-main">
                <div class="mod_location">
                    <router-link to="/center/my_settings_profile" class="on" tag="a"></router-link>
                   <router-link to="/center/my_settings_avatar" tag='a'></router-link>
                    <!-- <a href="#"></a> -->
                </div>
                <div class="mod_wrap">
                    <!-- <form class="mr_edit" action=""> -->
                    <div class="mr_edit">
                        <div>
                            <p></p>
                            <input type="text" v-model="userInfo.nickname" />
                        </div>
                        <div>
                            <p></p>
                            <input type="text" v-model="userInfo.mobile" />
                        </div>
                        <div>
                            <p></p>
                            <input type="text" v-model="userInfo.email"  />
                        </div>
                        <div>
                            <p></p>
                            <input type="text" v-model="userInfo.realname"  />
                        </div>
                        <div>
                            <p></p>
                            <div>
                                <select v-model="userInfo.gender">
                                    <option value=""></option>
                                    <option value=""></option>
                                    <option value=""></option>
                                </select>
                            </div>
                        </div>
                        <div>
                            <button class="submit" @click="postUserInfo" ></button>
                        </div>
                    </div>
                    <!-- </form> -->
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import '@/assets/css/center_user.css';
import {getUserInfoApi,editUserInfoApi} from '@/api/user'
export default {
    data(){
        return{
            userInfo:{
                nickname : '',
                mobile : '',
                email:'',
                realname:'',
                gender:''
            }
        }
    },
     mounted(){
        this.getUserInfo();
    },
    methods:{
       async getUserInfo(){
           let res = await getUserInfoApi();
           if(res && !res.data.code){
               let resdata = res.data.user
               this.userInfo.nickname = resdata.nickname;
               this.userInfo.mobile = resdata.mobile;
               this.userInfo.email = resdata.email;
               this.userInfo.realname = resdata.realname;
               this.userInfo.gender = resdata.gender;
           }
        },
       async postUserInfo(){
            let res = await editUserInfoApi(this.userInfo);
            if(res && !res.data.code){
                this.getUserInfo();
            }
        }
    }

}
</script>


