<template>
    <div class="center">
        <div class="top-bar">
            <ul class="bar-left left">
                <li><router-link to="/" tag="a"></router-link></li>
                <li><router-link to="/list" tag="a"></router-link></li>
            </ul>
            <div style="float:right"> 
                <img :src="topavatar" class="avatar" style="width:32px;height:32px;border-radius:50%">
                <router-link to="/center/my_settings_profile" tag="a">{{this.$store.state.user.username}}</router-link>
                <span>|</span>
                <router-link to="" @click.prevent="logout" tag="a"></router-link> 
            </div>
        </div>
        <div class="c-wrap">
            <div class="c-left">
                <div class="c-menu">
                    <ul>
                        <li>
                            <router-link to="/center/my_settings_profile" tag="a"></router-link>
                        </li>
                        <li>
                            <router-link to="/center/my_recipe_me" class="on" tag="a"></router-link>
                        </li>
                    </ul>
                </div>
            </div>
            <div class="c-main">
                <div class="mod_location">
                    <router-link to="#" class="on" tag="a"></router-link>
                    <!-- <a href="#"></a>
                    <a href="#"></a> -->
                </div>
                <div class="mod_wrap">
                    <!-- <form class="addmenuform" action=""> -->
                        <div>
                            <p></p>
                            <input name="menuname" type="text" v-model="data.name" />
                        </div>
                        <div>
                            <p></p>
                            <select id="sendSybol" v-model="data.categoryId">
                                <option  v-for="(item,index) in categories" :key="index" :value="item.id">{{item.name}}</option>
                            </select>

                        </div>
                        <div>
                            <p>9</p>
                            <label class="filelabel">
                                <span></span>
                                <input name="addfile" type="file" multiple id="uploadImg" @change="handleUpload($event)" />
                            </label>
                            <ul class="thumb_list">
                                <li v-for="cover in data.covers" :key="cover.id">
                                    <img :src="`/public/uploads/cookbooks/${cover}`" alt="">
                                </li>
                            </ul>
                            <textarea name="describe" placeholder="500" v-model="data.description"></textarea>
                        </div>
                        <div>
                            <p></p>
                            <ul class="optionlist clearfix">
                                <li>
                                    <Single param-title="" :list='params.levels' @selecthandle='currentLevel'></Single>
                                </li>
                                <li>
                                    <Single param-title="" :list='params.tastes' @selecthandle='currentTeste'></Single>
                                </li>
                                <li>
                                    <Single param-title="" :list='params.crafts' @selecthandle='currentCraft'></Single>
                                </li>
                                <li>
                                    <Single param-title="" :list='params.needTimes' @selecthandle='currentTime'></Single>
                                </li>
                                <li>
                                    <Multiple param-title="" :list='params.cookers' @selecthandle='currentCooker'></Multiple>
                                    
                                </li>
                            </ul>
                        </div>
                        <div>
                            <p></p>
                            <div class="addfood">
                                <AddItem title="" :value='data.ingredients.m'></AddItem>
                                <AddItem title="" :value='data.ingredients.s'></AddItem>
                            </div>
                        </div>
                        <Steps :value='data.steps'></Steps>
                        <div>
                            <p class="unrequired"></p>
                            <textarea class="tips" name="" placeholder="500" v-model='data.tips'></textarea>
                        </div>
                        <button class="release" @click="postCookBooks"></button>
                    <!-- </form> -->
                </div>
            </div>
        </div>
        
    </div>
</template>
<script>
import '@/assets/css/user_form.css';
import '@/assets/css/user_publish.css';
import '@/assets/css/center_user.css';
import {postCookbookPicApi,postPublishApi} from '@/api/user';

import Single from '@/components/single.vue';
import Multiple from '@/components/multiple.vue';
import AddItem from '@/components/addItem.vue';
import Steps from '@/components/steps.vue';

export default {
    components:{
        Single,
        Multiple,
        AddItem,
        Steps
    },
    data(){
        return{
            topavatar : `http://localhost:8888/public/uploads/avatar/${this.$store.state.user.avatar}`,
            data : {
                name:'',
                categoryId:'',
                covers:[],
                description:'',
                level:'',
                taste:'',
                craft:'' ,
                cookers:[],
                ingredients:{
                    m:[],
                    s:[]
                },
                steps:[],
                tips:''  
            },
            params:{
                levels:['','','',''],
                tastes:['','','','','','','','',''],
                crafts:['','','','','','','','',''],
                needTimes:['','','','','','','',''],
                cookers:['','','','','','','','']           
            },
            
            
        }
    },
    computed:{
      categories(){
          return this.$store.state.category.category
      }
    },
    mounted:function(){
        //statecategory storeaction 
         this.$store.dispatch('asyncGetdata');
    },
    methods:{
        //
        async handleUpload(e){
            // console.log(e.target.files);
            var files = [...e.target.files];
            files.forEach(async file=>{
                 let fd = new FormData();
                 fd.append('cover',file);
                 let resCookbookPic = await postCookbookPicApi(fd); 
                 if(resCookbookPic && !resCookbookPic.data.code){
                    // console.log(resCookbookPic.data.coverUrl);
                    this.data.covers.push(resCookbookPic.data.coverUrl);
                    // console.log(this.data.covers);
                }
            })
        },
        currentLevel(cur){
            this.data.level = cur;
        },
        currentTeste(cur){
            this.data.taste = cur;
        },
        currentCraft(cur){
            this.data.craft = cur;
        },
        currentTime(cur){
            this.data.needTime = cur;
        },
        currentCooker(cur){
            this.data.cookers = cur;
        },
        //
        async postCookBooks(){
            let res = await postPublishApi(this.data);
        }

    }
}
</script>


