<template>
    <ul class="menu">
        <li v-for="(item) in categories" :key="item.id">
            <!-- <a :href="`/list/${item.id}`">{{item.name}}</a> -->
            <router-link :to="`/list/${item.id}`" tag="a">{{item.name}}</router-link>
            <ul class="sub" v-if="item.children.length">
                <li v-for="(child) in item.children" :key="child.id">
                    <!-- <a :href="`/list/${child.id}`">{{child.name}}</a> -->
                    <router-link :to="`/list/${child.id}`" tag="a">{{child.name}}</router-link>
                </li>
               
            </ul>
        </li>
        
    </ul>
</template>
<script>
export default {
  name: 'indexleftmenu',
  data(){
      return{
      }
  },
  computed:{
      categories(){
          return this.$store.getters.getIndexCategory
      }
  },
  mounted:function(){
      //statecategory storeaction 
      this.$store.dispatch('asyncGetdata');
  }
}
</script>
