<template>
    <div class="center">
        <div class="top-bar">
            <ul class="bar-left left">
                <li><router-link to="/" tag="a"></router-link></li>
                <li><router-link to="/list" tag="a"></router-link></li>
            </ul>
            <div style="float:right"> 
                <img :src="topavatar" class="avatar" style="width:32px;height:32px;border-radius:50%">
                <router-link to="/center/my_settings_profile" tag="a">{{this.$store.state.user.username}}</router-link>
                <span>|</span>
                <router-link to="" @click.prevent="logout" tag="a"></router-link>
            </div>
        </div>
        <div class="c-wrap">
            <div class="c-left">
                <div class="c-menu">
                    <ul>
                        <li>
                            <router-link to="/center/my_settings_profile" class="on" tag="a"></router-link>
                        </li>
                        <li>
                            <router-link to="/center/my_recipe_me" tag="a"></router-link>
                        </li>
                    </ul>
                </div>
            </div>
            <div class="c-main">
                <div class="mod_location">
                    <router-link to="/center/my_settings_profile" tag="a"></router-link>
                    <router-link to="/center/my_settings_avatar" class="on" tag="a"></router-link>
                    <!-- <a href="#"></a> -->
                </div>
                <div class="mod_wrap">
                    <!-- <form class="mr_edit" action=""> -->
                        <div class="mr_edit">
                        <div>
                            <p></p>
                            <img class="imgLoad" :src="avatar" style="display: block;" width="120" height="120">
                            <input name="avatarpic" class="avatarInput" @change='avatarHandle($event)' type="file" accept="image/gif,image/jpeg,image/jpg,image/png">

                        </div>
                        <div>
                            <button class="submit" @click="upload" ></button>
                        </div>
                        </div>
                    <!-- </form> -->
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import '@/assets/css/center_user.css';
import {postAvatarApi} from '@/api/user';
// import axios from 'axios';
export default {
    data(){
        return{
            file : '',
            avatar :`/public/uploads/avatar/${this.$store.state.user.avatar}`,
            topavatar : `/public/uploads/avatar/${this.$store.state.user.avatar}`,
            userinfo:{
                nickname : '',
                mobile : '',
                email:'',
                realname:'',
                gender:'',
                birthday:''
            }
        }
    },
    methods:{
        avatarHandle(e){
            let file = e.target.files[0];
            if(file){
                this.file = file;
                // console.log(file);
                let render = new FileReader();
                let that = this;
                render.readAsDataURL(file);
                render.onload = function(e) {
                    that.avatar = this.result;
                }
            }
        },
        async upload(){
            let fd = new FormData();
            fd.append('avatar',this.file);
            let resAvatar = await postAvatarApi(fd);

            if(resAvatar && !resAvatar.data.code){
                this.avatar = `/public/uploads/avatar/${resAvatar.data.avatarUrl}`;
                this.topavatar = `/public/uploads/avatar/${resAvatar.data.avatarUrl}`;
                // console.log(this.topavatar);
            }
        }
    }
}
</script>
