import {getCategory} from '@/api/index';
import {Tree} from '@/libs/tree';

const category = {
    state:{
        category:[]
    },
    mutations:{
        getdata(state, params) {
            state.category = params.category;
            localStorage.category = JSON.stringify(state.category); //  localStorage 
          }
    },
    actions:{
        async asyncGetdata(context){
            let res = await getCategory();
            if(res.data){
                context.commit('getdata', {category: res.data.categories});
                return res.data;
                
            }
        }
    },
    getters:{
        getIndexCategory(state){
            let data = (new Tree(state.category)).getTree(0);
            data = data.filter(item => item.id < 6);
            return data
        },
        getCategory(state){
            return state.category
        }
    }
};

export default category;