<template>
    <div>
        <div class="select clearfix">
            <span>{{paramTitle}}</span>
            <em class="more_option">{{data.level.join(',')}}</em>
            <i @click="togglePanel($event)"></i>
        </div>
        <div class="selectoption" v-show="isHidden">
            <span 
                :data-value="index" 
                v-for="(item,index) in list" 
                :key="item.id"
                @click="selectHandle(index,item,$event)"
            >{{item}}</span>
        </div>
    </div>
</template>
<script>
export default {
    props:['paramTitle','list'],
    data(){
        return{
            data:{
                level:[]
            },
            selectOpt:'',
            isHidden:false
            
        }
    },
    created(){
        let _this = this;
        document.addEventListener('click',function() {
            _this.isHidden = false;
        })
    },
    methods:{
        selectHandle(index,item,e){
            var $el = e.target;
            var index = this.data.level.indexOf(item);
            if(index > -1){
                //
                this.data.level.splice(index,1);
                $el.className = '';
            }else{
                //
                this.data.level.push(item);
                $el.className = 'on';
            }
            this.$emit('selecthandle', this.data.level);
            e.stopPropagation();
        },
        togglePanel(e){
            this.isHidden = !this.isHidden;
            e.stopPropagation();
        }
    }
}
</script>

