<template>
    <section class="dish_display">
        <header class="dish_header">
            <nav class="dish_list">
                <a class="on" href="javascript:;"></a>
                <!-- <a href="javascript:;"></a> -->
            </nav>
            <nav class="dish_link">
                <!-- <a href="/list/13"></a>
                <a href="/list/14"></a>
                <a href="/list/15"></a>
                <a href="/list/16"></a>
                <a href="/list/17"></a>
                <a href="/list/18"></a> -->

                <router-link to="/list/13" tag="a"></router-link>
                <router-link to="/list/14" tag="a"></router-link>
                <router-link to="/list/15" tag="a"></router-link>
                <router-link to="/list/16" tag="a"></router-link>
                <router-link to="/list/17" tag="a"></router-link>
                <router-link to="/list/18" tag="a"></router-link>
            </nav>
        </header>
        <div class="dish_content">
            <ul>
                <li v-for="(cookbook) in cookbooks" 
                    :key="cookbook.id"
                >
                    <figure>
                        <div class="dish_img">
                             <router-link :to="`/view/${cookbook.id}`" tag="a">
                            <img :src="`/public/uploads/cookbooks/${cookbook.cover}`" alt="" />
                             </router-link>
                        </div>
                        <figcaption>
                            <i>
                                <router-link :to="`/view/${cookbook.id}`" tag="a">
                                    {{cookbook.name}}
                                </router-link>
                            </i>
                            <span>
                                <router-link :to="`/home/space-recipe/${cookbook.userId}`" tag="a">
                                    {{cookbook.username}}
                                </router-link>
                            </span>
                        </figcaption>
                    </figure>
                </li>
            </ul>
        </div>
    </section>
</template>
<script>
import { getCookbookApi } from '@/api/index';
export default {
    data(){
        return{
            cookbooks:[]
        }
    },
    mounted:function(){
        this.getCookbook();
    },
    methods:{
        async getCookbook(){
            let res = await getCookbookApi();
            if(!res.code){
                //
               this.cookbooks = res.data.cookbook;
            //    console.log(this.cookbooks);
            }
        }
    }
}
</script>

