<template>
    <div>
        <h4>{{title}}</h4>
        <ul class="foodlist"> 
            <li v-for="(item, index) in items" :key="index" class="foodnum clearfix"> 
                <input type="text" placeholder="" v-model="item.k" @change="handleName(index,$event)" />
                <input type="text" placeholder="" v-model="item.v" @change="handleAmount(index,$event)"/>
                <button class="hidden" @click="del(index)">X</button>
            </li>
        </ul>
        <button class="addfoodgroup" type="button" @click="addItem">{{title}}</button>
    </div>
</template>
<script>
export default {
    props:['title','value'],
    data(){
        return{
            items: this.value
        }
    },
    watch:{
        items:function(val){
            this.$emit('input',val)
            this.$emit('on-change',val);
        },
        value:function (val) {
            this.updateItem(val);
        }
    },
    methods:{
        // 
        addItem() {             
            this.items.push({k:'',v:''});
        }, 
        // 
        del(index) { 
            this.items.splice(index, 1); 
        },
        handleName(index,event){
            var val = event.target.value.trim();
            this.items[index].k = val;
        },
        handleAmount(index,event){
            var val = event.target.value.trim();
            this.items[index].v = val;
        },
        updateItem(val){
            this.items = val;
        }
    },
    mounted(){
        this.updateItem(this.value);
    }
}
</script>

