import Vue from 'vue'
import Router from 'vue-router'

import Home from './views/Home.vue'
// import List from './views/List.vue'

import SpaceRecipe from './views/member/space-recipe.vue'
import SpaceFavrecipe from './views/member/space-favrecipe.vue'
import mySettingsProfile from './views/center/my_settings_profile.vue'
import myRecipeMe from './views/center/my_recipe_me.vue'
import mySettingsAvatar from './views/center/my_settings_avatar.vue'
import test from './views/test.vue'

import storeToken from 'store';
import store from './store/index'


Vue.use(Router)

const router =  new Router({
  // mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      name: 'home',
      meta:{
        title:''
      },
      component: Home
    },
    {
      path: '/list/:id?',
      name: 'list',
      meta:{
        title:'_'
      },
      component: (resolve) => require(['./views/List.vue'],resolve)
    },
    {
      path: '/view/:id',
      name: 'view',
      meta:{
        title:'_'
      },
      component: () => import('./views/View.vue')
    },
    {
      path: '/register',
      name: 'register',
      meta:{
        title:'_'
      },
      component: () => import('./views/user/Register.vue')
    },
    {
      path: '/login',
      name: 'login',
      meta:{
        title:'_'
      },
      component: () => import('./views/user/Login.vue')
    },
    {
      path: '/home/space-recipe/:id',
      name: 'SpaceRecipe',
      meta:{
        title:'_',
        requireAuth:true
      },
      component: SpaceRecipe
    },
    {
      path: '/home/space-favrecipe',
      name: 'SpaceFavrecipe',
      meta:{
        title:'_',
        requireAuth:true
      },
      component: SpaceFavrecipe
    },
    {
      path: '/center/my_settings_profile',
      name: 'mySettingsProfile',
      meta:{
        title:'_',
        requireAuth:true
      },
      component: mySettingsProfile
    },
    {
      path: '/center/my_recipe_me',
      name: 'myRecipeMe',
      meta:{
        title:'_',
        requireAuth:true
      },
      component: myRecipeMe
    },
    {
      path: '/center/my_settings_avatar',
      name: 'mySettingsAvatar',
      meta:{
        title:'_',
        requireAuth:true
      },
      component: mySettingsAvatar
    },
    {
      path: '/center/test',
      name: 'test',
      meta:{
        title:'test',
        requireAuth:true
      },
      component: test
    },

  ]
});

//
router.beforeEach((to,from,next)=>{
  const token = storeToken.get('loginedtoken');
  // console.log(from);

  // chrome
  document.body.scrollTop = 0
  // firefox
  document.documentElement.scrollTop = 0
  // safari
  window.pageYOffset = 0


  if(token){
    //token,store
    store.dispatch('GetUserInfo').then(res=>{
      next();
    })
    // next();
  }else{
    //
    if(to.meta.requireAuth){
      // 
      next({
        path:'/login'
      })
    }else{
      next();
    }
    
  }
  window.document.title = to.meta.title;
})



 export default router;
