// import axios from 'axios';
import fetch from '@/libs/fetch';

export function getCategory() {
    return fetch({
        method:'get',
        url:'/api/index/category'
    });

  }

  export function getCookbookApi() {
    return fetch({
        method:'get',
        url:'/api/index/cookbookindex'
    });

  }

  export function getListApi(params) {
    return fetch({
        method:'get',
        url:'/api/index/list',
        params
    });
  }

  export function getViewApi(params) {
    return fetch({
        method:'get',
        url:'/api/index/view',
        params
    });
  }

  export function getCommentApi(params) {
    return fetch({
        method:'get',
        url:'/api/index/comment',
        params
    });
  }

  export function postCommentApi(params) {
    return fetch({
        method:'post',
        url:'/api/index/comment/add',
        data:params
    });
  }

  //
  export function getSpaceUserApi(params) {
    return fetch({
        method:'get',
        url:'/api/index/spaceuser',
        params
    });
  }

    //
    export function getSpaceCookbookApi(params) {
        return fetch({
            method:'get',
            url:'/api/index/spacecookbook',
            params
        });
      }