<template>
    <div>
        <div class="select clearfix">
            <span>{{paramTitle}}</span>
            <em>{{data.level}}</em>
            <i @click="togglePanel($event)"></i>
        </div>
        <div class="selectoption" v-show="isHidden">
            <span 
                :data-value="index" 
                v-for="(item,index) in list" 
                :key="item.id"
                @click="selectHandle(index,item,$event)"
                :class='index==current? "on": ""'
            >{{item}}</span>
        </div>
    </div>
</template>
<script>
export default {
    props:['paramTitle','list','paramValue'],
    data(){
        return{
            currentLevel:this.paramValue,
            data:{
                level:''
            },
            selectOpt:'',
            isHidden:false,
            current:-1
            
        }
    },
    created(){
        let _this = this;
        document.addEventListener('click',function() {
            _this.isHidden = false;
        })
    },
    methods:{
        selectHandle(index,item,e){
            this.current = index;
            this.data.level = item;
            this.$emit('selecthandle', item);
            e.stopPropagation();
        },
        togglePanel(e){
            this.isHidden = !this.isHidden;
            e.stopPropagation();
        }
    }
}
</script>


