<template>
    <div>
        <div class="myself_bg">
            <div class="wrap">
                <div class="self_msg">
                    <div class="self_img">
                        <img :src="`/public/uploads/avatar/${this.userInfo.avatar}`" alt="">
                    </div>
                    <div class="self_name">
                        <i>{{this.userInfo.nickname}}</i>
                        <span>{{this.userInfo.createdAt}} </span>
                    </div>
                </div>
                <div class="self_visit" v-show="false">
                    <ul>
                        <li>
                            <i>0</i>
                            <span></span>
                        </li>
                        <li>
                            <i>16</i>
                            <span></span>
                        </li>
                        <li>
                            <i>1</i>
                            <span></span>
                        </li>
                    </ul>
                </div>
            </div>
        </div>
        <nav class="self_nav">
            <div class="wrap">
                <ul>
                    <li><router-link to="/index" tag="a"></router-link></li>
                    <li><router-link :to="`/home/space-recipe/${this.userInfo.userId}`"></router-link></li>
                    <!-- <li><a href="/home/space-favrecipe"></a></li> -->
                </ul>
            </div>
        </nav>
    </div>
</template>
<script>
import {getSpaceUserApi} from '@/api/index';
export default {
    props:['userId'],
    data(){
        return{
            userInfo:{},
            params:{
               userId:this.userId
            }
        }
    },
    mounted(){
        this.getSpaceUser();
    },
    methods:{
        async getSpaceUser(){
            let res = await getSpaceUserApi(this.params);
            if(res && !res.data.code){
                this.userInfo = res.data.data;
                this.$emit('nickname',res.data.data.nickname)
            }
        }
    }
}
</script>

